// src/components/CommentForm.js
import React, { useState } from 'react';
import '../css/review_detail.css';

const CommentForm = ({ onCommentSubmit }) => {
  const [username, setUsername] = useState('');
  const [content, setContent] = useState('');
  const [rate, setRate] = useState(0); //  0 

  const handleSubmit = (e) => {
    e.preventDefault();

    //      
    onCommentSubmit({ username, content, rate });

    //  
    setUsername('');
    setContent('');
    setRate(0);
  };

  return (
    <form onSubmit={handleSubmit}>
        <div className='rd_comment_box'>
        <div className='rd_input'>
      <label>
        <input type="text" className='rd_comment_input' placeholder="" value={username} onChange={(e) => setUsername(e.target.value)} />
      </label>
      <br />
        </div>
      <label>
        <textarea value={content} className='rd_comment_content' placeholder=" " onChange={(e) => setContent(e.target.value)} />
      </label>
      <br />
      <label>
        <span className='rd_rate'>
        :
        </span>
        <select value={rate} onChange={(e) => setRate(Number(e.target.value))}>
          <option value={1}>1</option>
          <option value={2}>2</option>
          <option value={3}>3</option>
          <option value={4}>4</option>
          <option value={5}>5</option>
        </select>
      </label>
      <br />
      <button className='rd_submit' type="submit"></button>
      </div>
      <div className='footer'></div>
    </form>
  );
};

export default CommentForm;
