import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import '../css/write.css';

const M_PostForm = ({ onPostCreated }) => {
  const [title, setTitle] = useState('');
  const [author, setAuthor] = useState('');
  const [content, setContent] = useState('');
  const [image, setImage] = useState(null);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      //   ISO  
      const timestamp = new Date().toISOString();

      const response = await axios.post('http://cyclone.dothome.co.kr/board', {
        title,
        author,
        content,
        timestamp,
      });

      if (onPostCreated) {
        onPostCreated(response.data);
      }

      //  
      setTitle('');
      setAuthor('');
      setContent('');

      //    
      navigate('/board');
    } catch (error) {
      console.error('Error creating post:', error);
    }
  };

  return (
    <div className='main_write'>
      <h2> </h2>
      <div className='write_wrap'>
      <form onSubmit={handleSubmit}>
        <div className='nick_line_write'>
        <label className='nickname_write'>
          <input type="text" placeholder='' value={author} onChange={(e) => setAuthor(e.target.value)} />
        </label>
        </div>
       
        <div className='title_line_write'>
        <label className='title_write'>
          <input type="text" placeholder=' ' value={title} onChange={(e) => setTitle(e.target.value)} />
        </label>
        </div>
       
        <div className='line_write'>
        <label className='content_write'>
          <textarea placeholder=' ' value={content} onChange={(e) => setContent(e.target.value)} />
        </label>
      
        </div>
        <button className='submit_write' type="submit"></button>
      </form>
      </div>
      <div className='footer'></div>
    </div>
  );
};

export default M_PostForm;
