import '../../src/css/login.css';
import { useState } from 'react';
import { useEffect } from 'react';
//import Home from '../home';
//import Home1 from './home1';
import Home2 from './home2';

function Login(props) {
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  
  return <>
  <div className='background1'>
  <h2></h2>
  
  <div className="form">
      <p><input className="login" type="text" name="username" placeholder="" onChange={event => {
        setId(event.target.value);
      }} /></p>
      <p><input className="login" type="password" name="pwd" placeholder="" onChange={event => {
        setPassword(event.target.value);
      }} /></p>

      <p><input className="btn" type="submit" value="" onClick={() => {
        const userData = {
          userId: id,
          userPassword: password,
        };
        fetch("http://localhost:3001/login", { //auth   
        method: "post", // method :
        headers: {      // headers: API    
          "content-type": "application/json",
        },
        body: JSON.stringify(userData), //userData  
      })
      .then((res) => res.json())
      .then((json) => {            
        if(json.isLogin==="True"){
          props.setMode("WELCOME");
        }
        else {
          alert(json.isLogin)
        }
      });
    }} /></p>
    </div>

    <p> ?  <button onClick={() => {
      props.setMode("SIGNIN");
    }}></button></p>
  </div>
  </>

}


function Signin(props) {
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");

  return <>
  <div className='background1'>
    <h2></h2>

    <div className="form">
      <p><input className="login" type="text" placeholder="" onChange={event => {
        setId(event.target.value);
      }} /></p>
      <p><input className="login" type="password" placeholder="" onChange={event => {
        setPassword(event.target.value);
      }} /></p>
      <p><input className="login" type="password" placeholder=" " onChange={event => {
        setPassword2(event.target.value);
      }} /></p>

      <p><input className="btn" type="submit" value="" onClick={() => {
        const userData = {
          userId: id,
          userPassword: password,
          userPassword2: password2,
        };
        fetch("http://localhost:3001/signin", { //signin   
          method: "post", // method :
          headers: {      // headers: API    
            "content-type": "application/json",
          },
          body: JSON.stringify(userData), //userData  
        })
          .then((res) => res.json())
          .then((json) => {
            if(json.isSuccess==="True"){
              alert(' !')
              props.setMode("LOGIN");
            }
            else{
              alert(json.isSuccess)
            }
          });
      }} /></p>
    </div>

    <p>   <button onClick={() => {
      props.setMode("LOGIN");
    }}></button></p>
  </div>
  </>
}

function App() {
  const [mode, setMode] = useState("");

  useEffect(() => {
    fetch("http://localhost:3001/authcheck")
      .then((res) => res.json())
      .then((json) => {        
        if (json.isLogin === "True") {
          setMode("WELCOME");
        }
        else {
          setMode("LOGIN");
        }
      });
  }, []); 

  let content = null;  

  if(mode==="LOGIN"){
    content = <Login setMode={setMode}></Login> 
  }
  else if (mode === 'SIGNIN') {
    content = <Signin setMode={setMode}></Signin> 
  }
  else if (mode === 'WELCOME') {
    content = <>
    <Home2/>
    </>
  }

  return (
    <>
      <div className="background">
        {content}
      </div>
    </>
  );
}

export default App;
