import './css/signup.css';
import Logo from './img/logo.PNG';
import { useState } from "react"; 
import Box from "@mui/material/Box"; 
import Button from "@mui/material/Button"; 
import Typography from "@mui/material/Typography";
import CustomModal from "./CustomModal_Signiup";
import { useEffect } from 'react';

function Signup(props) {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const openModal = () => setIsModalOpen(true); 
  const closeModal = () => setIsModalOpen (false);

  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");


  return (   
  <div className='signup'>
  <Button onClick={openModal}>Signup</Button> 
  <CustomModal
  isOpen={isModalOpen} 
  closeModal={closeModal}
  >
      <Box>
  <div className="member">
    {/* 1.  */}
    <img className="logo" src={Logo} alt="Logo" />
    {/* 2.  */}
    <div className="field">
      <b></b>
      <span className="placehold-text">
      <input className="login" type="text" placeholder="" onChange={event => {
        setId(event.target.value);
      }} />
      </span>
    </div>
    <div className="field">
      <b></b>
      <input className="login" type="password" placeholder="" onChange={event => {
        setPassword(event.target.value);
      }} />
    </div>
    <div className="field">
      <b> </b>
      <input className="login" type="password" placeholder=" " onChange={event => {
        setPassword2(event.target.value);
      }} />
    </div>
    <div className="field">
      <b></b>
      <input type="text" />
    </div>
    {/* 3. () */}
    <div className="field gender">
      <b></b>
      <div>
        <label>
          <input type="radio" name="gender" />
          
        </label>
        <label>
          <input type="radio" name="gender" />
          
        </label>
      </div>
    </div>
    {/* 4.   */}
    <div className="body-size">
      <b></b>
      <div>
        <label>
          <input type="text" name="height" />
        </label>
        <b></b>
        <label>
          <input type="text" name="weight" />
        </label>
      </div>
    </div>
    {/* 5.   */}
    <input className="btn" type="submit" value="" onClick={() => {
        const userData = {
          userId: id,
          userPassword: password,
          userPassword2: password2,
        };
        fetch("http://localhost:3001/signin", { //signin   
          method: "post", // method :
          headers: {      // headers: API    
            "content-type": "application/json",
          },
          body: JSON.stringify(userData), //userData  
        })
          .then((res) => res.json())
          .then((json) => {
            if(json.isSuccess==="True"){
              alert(' !')
              props.setMode("LOGIN");
            }
            else{
              alert(json.isSuccess)
            }
          });
      }} />
    {/* 6.  */}
    <div className="member-footer">
      <div>
        <a href="#none"></a>
        <a href="#none"></a>
        <a href="#none">  </a>
        <a href="#none"> </a>
      </div>
      <span>
        <a href="#none">health_review Corp.</a>
      </span>
    </div>
  </div>
      </Box>
  </CustomModal>
  </div>
  );
}
export default Signup;